.footer {
  background: var(--color-primary-variant);
  color: var(--color-black);
  margin-bottom: 0;
  box-shadow: 0 0 5px #0003;
  font-size: 0.8rem;
  line-height: 2;
  padding: 1rem 0;
}
.footer a, .footer a:hover {
  color: var(--color-dark-variant);
}
.footer a:hover {
  text-decoration: underline solid 1px;
}

.footer hr {
  border-bottom: 1px solid var(--color-dark);
}

.footer__links {
  display: flex;
  margin: 1rem 0 2rem;
}

.footer__link {
  flex-grow: 1;
}

.foot__title {
  font-weight: 500;
}
.foot__title label {
  display: none;
}

.foot__body {
  font-weight: lighter;
}

.in__section {
  margin-top: 0.5rem;
}

/* call centr */
.call__centr {
  line-height: 1.2;
  margin-bottom: 5px;
}
.call__centr a {
  letter-spacing: 0.5px;
}

/* footer info */
.footer__info {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 768px){
  .footer__links {
    flex-wrap: wrap;
    gap: 3rem;
  }

  .call__centr span {
    display: block;
  }
}