.navbar {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid var(--color-bg-variant);
  position: sticky;
  top: 0;
  z-index: 999;
  padding: 0;
  background: var(--color-bg-variant);
  box-shadow: 0 2px 5px #0003;
  transition: 0.8s all ease;
  border-radius: 0;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

.nav {
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.colors {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

.clr {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid var(--color-dark-variant);
  box-shadow: 2px 2px 5px #0003;
  transition: var(--transition);
  opacity: 0.9;
  cursor: pointer;
}
.clr:hover {
  box-shadow: 2px 4px 5px #0005;
  opacity: 1;
}

.light {
  background: linear-gradient(45deg, #fff, #f8f8f8);
}
.dark {
  background: linear-gradient(45deg, #252529, #29292f);
}
.primary {
  background: linear-gradient(45deg, #bcf1f1, #d3feff);
}

.menu__icon {
  line-height: 50px;
  padding: 0 15px;
  height: 50px;
  font-size: 1.5rem;
  color: var(--color-primary);
  cursor: pointer;
  position: fixed;
  z-index: 1000;
  right: 15px;
  top: 5px;
  border-radius: 10px;
  box-shadow: 0 0 5px #0003;
  background: var(--color-bg-variant);
  transition: var(--transition);
  display: none;
  user-select: none;
}
.close-nav {
  display: none;
}

.navCheckClosser {
  position: fixed;
  top: 50%;
  left: 100%;
  width: 100%;
  height: 0vh;
  z-index: 100;
  background: #0003;
  transition: var(--transition);
  opacity: 0;
  border-radius: 50%;
}

#navCheck:checked ~ .menu__icon {
  background: #e64747;
  color: #fff;
}
#navCheck:checked ~ .menu__icon .show-nav {
  display: none;
}
#navCheck:checked ~ .menu__icon .close-nav {
  display: inline-block;
}
#navCheck:checked ~ .navCheckClosser {
  transform: scale(1);
  height: 100vh;
  opacity: 1;
  border-radius: 0;
  top: 0;
  left: 0;
}

.nav__items {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
}
.nav__items a:hover {
  color: var(--color-primary-variant);
  border-bottom-color: var(--color-primary-variant);
}

.nav__item {
  color: var(--color-primary);
}

.download {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}

.download:hover button {
  background: var(--color-bgx);
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
}

.download button {
  background: var(--color-primary);
  color: var(--color-bgx);
  letter-spacing: 0.3px;
  font-weight: 700;
}

.download:hover .downloads {
  width: 250px;
  height: 80px;
  transform: rotate(0);
  left: 0;
  opacity: 1;
}

.downloads {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  top: 100%;
  left: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--color-bgx);
  border-radius: 5px;
  box-shadow: 0 0 5px #0003;
  transition: var(--transition);
  transform: rotate(-15deg) translate(-50px, -30px) scale(0);
  opacity: 0;
}

.downloads a {
  padding: 5px 10px;
  width: 100%;
  white-space: nowrap;
}
.downloads a:first-child {
  border-bottom: 1px solid #aaa;
}

@media (max-width: 768px) {
  .navbar {
    height: 380px;
    position: fixed;
    overflow: hidden;
    border-radius: 300px;
    left: 150%;
    top: -150px;
    transform: rotate(-45deg);
    opacity: 0;
    padding-bottom: 3rem;
  }
  #navCheck:checked ~ .navbar {
    top: 0;
    left: 0;
    transform: rotate(0deg);
    border-radius: 0 0 20px 20px;
    opacity: 1;
  }
  .navbar .nav {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  .navbar .nav__items {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .menu__icon {
    display: inline-block;
  }
  .downloads {
    left: -50%;
  }
}