.logo__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0;
  text-align: center;
}

.logo__box h5 {
  color: var(--color-dark-variant);
}

.logo_app {
  width: 150px;
  margin: 1rem 0;
}

.app__client {
  margin-bottom: 6rem;
  text-align: center;
}

.app__client h3 {
  /* border-bottom: 1px solid var(--color-dark-variant); */
  width: 60%;
  margin: 2rem auto 1rem;
}

.app__box {
  gap: 6rem;
  justify-content: center;
  align-items: stretch;
  flex-grow: 1;
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
  border-radius: 20px;
  background: -webkit-linear-gradient(-45deg, var(--color-body), var(--color-body2));
  background: linear-gradient(-45deg, var(--color-body), var(--color-body2));
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.app__image__box {
  width: 100%;
}
.app__image {
  width: 100%;
  user-select: none;
}

.app .btn {
  color: var(--color-primary);
}
.app .btn span{
  font-weight: 500;
}

.app .btn::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  left: -100%;
  border-bottom: 4px solid var(--color-primary);
}
.app .btn::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  right: -300%;
  border-bottom: 4px solid var(--color-primary);
  transition: 1s all ease;
}

.app:hover .btn::before {
  left: 0;
  transition: var(--transition);
}
.app:hover .btn::after {
  right: 0;
}

@media (max-width: 1080px) {
  .app__box {
    gap: 1rem;
  }
}
@media (max-width: 768px) {
  .app__image {
    width: 80%;
    margin: auto;
  }
}
@media (max-width: 576px) {
  .app__box {
    flex-direction: column;
  }
  .app {
    flex-direction: row;
    height: 150px;
    align-items: flex-end;
  }
  .app__image__box, .app__image {
    height: 100%;
    width: auto;
  }
}