/* news */
.news h2 {
  margin-bottom: 0;
}

.news .nav {
  height: 50px;
  position: relative;
  line-height: 50px;
  padding: 0 15px;
}

.news .nav__item {
  cursor: pointer;
}
.news ul {
  padding: 0;
  transition: var(--transition);
}

.blogs {
  margin: 1rem 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: stretch;
  justify-content: space-between;
  justify-items: stretch;
}

.blog {
  flex-grow: 1;
  width: 30%;
  border-radius: 10px;
  padding:  1rem;
  padding-bottom: 0.3rem;
  box-shadow: 2px 2px 5px #0003;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  position: relative;
  background: var(--color-bg-variant);
}
.blog:hover {
  box-shadow: 2px 4px 10px #0003;
}
.blog:hover img{
  transform: translateY(-10px);
  filter: opacity(1);
}

.blog__image {
  width: 100%;
  height: 215px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  display: grid;
  flex-grow: 1;
  grid-template-rows: 1fr 1fr;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
}
.blog__image img {
  position: absolute;
  top: -3px;
  height: 110%;
  width: 100%;
  filter: opacity(0.95);
  z-index: -1;
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  z-index: 1;
}

.blog__title {
  color: var(--color-primary);
  font-size: 1.2rem;
}
.blog__text {
  color: var(--color-dark-variant);
  font-weight: 400;
  font-size: 16px;
  align-self: end;
  letter-spacing: 0.4px;
}
.blog__creator {
  color: var(--color-primary-variant);
  font-size: 0.8rem;
}
.blog__date {
  color: var(--color-primary-variant);
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
}



/* menu check news */
.menu__news {
  display: none;
  line-height: 30px;
  padding: 0 15px;
  margin-top: 0.5rem;
  height: 30px;
  font-size: 1.5rem;
  color: var(--color-primary);
  cursor: pointer;
  position: absolute;
  z-index: 99;
  left: 5px;
  top: 5px;
  border-radius: 5px;
  box-shadow: 0 0 5px #0003;
  background: var(--color-bg-variant);
  transition: var(--transition);
}
.close-nav, .navCheckClosser2 {
  display: none;
}

.navCheckClosser2 {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1001;
}

#navCheckNews:checked ~ .menu__news {
  background: #e64747;
  color: #fff;
  z-index: 1005;
  user-select: none;
}
#navCheckNews:checked ~ .menu__news .show-nav {
  display: none;
}
#navCheckNews:checked ~ .menu__news .close-nav {
  display: inline;
  z-index: 1002;
}
#navCheckNews:checked ~ .navCheckClosser2 {
  display: inline-block;
}


@media (max-width: 1400px) {
  .blog {
    width: 40%;
    flex-grow: 1;
  }
}
@media (max-width: 1024px) {
  .blog {
    width: 40%;
  }
  .news .nav__items {
    position: absolute;
    height: 0px;
    overflow: hidden;
    border-radius: 300px;
    left: -25%;
    top: -50px;
    transform: rotate(-45deg) scale(0);
    opacity: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    z-index: 98;
  }
  .menu__news {
    display: inline-block;
  }
  #navCheckNews:checked ~ .nav__items {
    position: absolute;
    top: 60%;
    left: 0;
    height: 300px;
    border-radius: 0 0 20px 20px;
    transform: rotate(0deg);
    background: var(--color-bg-variant);
    border-radius: 10px;
    box-shadow: 0 0 5px #0003;
    padding: 1rem;
    opacity: 1;
    -webkit-backdrop-filter: blur(15px);
    backdrop-filter: blur(15px);
    z-index: 1004;
  }
}
@media (max-width: 768px) {
  .blog {
    width: 100%;
  }
}