@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #bcf1f1;
  --color-bgx: #d3feff;
  --color-bg-variant: #bcf1f188;
  --color-primary: #1a80b3;
  --color-primary-variant: #1a80b388;
  --color-blue: #354ac3;
  --color-dark: #181818;
  --color-dark-variant: #181818aa;
  --color-black: #000;
  --color-white: #fff;
  --color-light: #fff9;

  --color-body: #ACB6E5;
  --color-body2: #74ebd5;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--color-bg);
  color: var(--color-dark);
  line-height: 1.7;
  overflow-x: hidden;
  transition: 0.8s all ease;
  margin-bottom: 0;
  min-height: 100vh;
  background: var(--color-body2);
  background: -webkit-linear-gradient(to right, var(--color-body), var(--color-body2));
  background: linear-gradient(to right, var(--color-body), var(--color-body2));
}

/* ============= GENERAL STYLES ============== */
.container {
  width: var(--container-width-lg);
  max-width: 2400px;
  margin: 0 auto;
}

.d-none {
  display: none;
}
.d-md-none {
  display: none;
}
.d-flex {
  display: flex;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
  color: var(--color-black);
}

h1 {
  font-size: 2.5rem;
}

section {
  margin: 4rem 0;
  transition: 0.8s all ease;
}

section > div > h2,
section > div > h5 {
  color: var(--color-dark-variant);
}

section > div > h2 {
  font-size: 2rem;
  color: var(--color-primary);
  margin-bottom: 2rem;
}

section > h5 {
  font-size: 1.5rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
  text-decoration: none;
  padding: 5px 0;
  border: 1px solid transparent;
}

a:hover {
  color: var(--color-primary-variant);
}

.btn {
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
  padding: 7px 15px;
  text-align: center;
  border-radius: 5px;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ============== MEDIA QUERIES (MEDIUM DEVICES) =============== */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  .view__news__data {
    position: relative;
    height: 70px;
    width: 100%;
  }
  .view__news__data div {
    position: absolute;
    width: 95%;
  }
  .view__news__date {
    top: 0px;
  }
  
  .view__news__title {
    top: 20px;
  }
  
  .view__news__body {
    bottom: 0px;
  }
}

/* ============== MEDIA QUERIES (SMALL DEVICES) =============== */
@media screen and (max-width: 768px) {
  .container {
    width: var(--container-width-sm);
  }
  h1, h2, h3, h4, h5, p, li, .blog__title, .blog__text, .sta__value, .sta__name, .blog__creator, .blog__date, .home__image, .logo_app, .app__image__box, .app__image__box + div {
    transform: scale(0.8);
  }

  .sta__value:hover {
    transform: scale(1);
  }
  .download:hover .downloads {
    transform: rotate(0) scale(0.8);
    left: -50%;
    top: 80%;
    opacity: 1;
  }

  section > div > h2 {
    margin-bottom: 1rem;
    font-size: 1.5rem;
  }
  .d-md-none {
    display: inline-block;
  }
}

@media screen and (max-width: 576px) {
  section > div > h2 {
    margin-bottom: 0.5rem;
    font-size: 1.2rem;
  }
}
