.sta__boxs {
  background: var(--color-bg-variant);
  border-radius: 20px;
  box-shadow: 2px 2px 10px #0003;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: stretch;
  padding: 1rem;
}

.sta__box {
  min-width: 180px;
  flex-grow: 1;
  background: var(--color-bg-variant);
  padding: 1.5rem;
  border-radius: 10px;
  text-align: center;
  box-shadow: 2px 2px 5px #0003;
  display: flex;
  gap: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sta__box:hover .sta__value {
  transform: scale(1.1);
  background: linear-gradient(60deg, var(--color-blue), var(--color-primary-variant));
}

.sta__value {
  width: 80px;
  height: 80px;
  line-height: 80px;
  border-radius: 50%;
  font-size: 1.2rem;
  background: linear-gradient(45deg, var(--color-blue), var(--color-primary-variant));
  color: var(--color-bg);
  transition: var(--transition);
}